import React, {Component} from "react";

export default class ItemDescription extends Component {

    render() {
        return (
            <div>

                <div>
                    Level: {this.props.item.level} {this.props.item.name}
                </div>

                <div>
                    {
                        this.props.item.atk ?
                            <span>Atk: {this.props.item.atk} </span> : null
                    }

                    {
                        this.props.item.atk ?
                            <span>Matk: {this.props.item.matk} </span> : null
                    }

                    {
                        this.props.item.def ?
                            <span>Atk: {this.props.item.def} </span> : null
                    }

                    {
                        this.props.item.mdef ?
                            <span>nAtk: {this.props.item.mdef} </span> : null
                    }
                </div>

                {
                    !this.props.item.equiped && true === false ?
                        <button className='rpgui-button'>
                            <span>Disentchant</span>
                        </button>
                        : null
                }

                {
                    this.props.item.equiped ?
                        <button className='rpgui-button'
                                onClick={() => this.props.onUnequip(this.props.item)}>
                            <span>Unequip</span>
                        </button>
                        :
                        <button className='rpgui-button'
                                onClick={() => this.props.onEquip(this.props.item)}>
                            <span>Equip</span>
                        </button>
                }

            </div>
        )
    }
}