import Delay from "../delay";
import {ItemCategories} from "../craefter/types";

export default class Item {

    equiped = false;

    constructor({
                    category = ItemCategories.Unknown,
                    name,
                    level = 1,
                    delay = global.delay || 50
                } = {}) {

        this.delay = new Delay(delay);

        this.category = category;
        this.name = name;
        this.level = level;
    }
}
